import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { HttpClient } from "@angular/common/http";
import { LoginService } from "../services/login.service";
import { MatSnackBar } from '@angular/material/snack-bar';
import { FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private router: Router, private http: HttpClient, private loginService: LoginService, private _snackBar: MatSnackBar, public fb: FormBuilder, ) { }
  totalList: any;
  isDataLoading = false;
  totalCountry = [];
  totalStates: any = [];
  totalDist: any = [];
  allSchool: any = [];
  currentSchool: any = {};
  registerObj: any = {
    country: '',
    state: '',
    district: '',
    schoolName: '',
    address: '',
    username: '',
    password: ''
  };
  loginObj: any = {
    country: '',
    state: '',
    district: '',
    schoolName: '',
    username: '',
    password: ''
  };
  allDistricSchool: any = [];
  myLoginForm;
  myRegisterForm;
  ngOnInit() {
    if (localStorage && localStorage.useradmin_data && localStorage.useradmin_data.length) {
      this.router.navigate(['/main/home']);
    }
    this.reactiveForm();
  }

  reactiveForm() {
    this.myLoginForm = new FormGroup({
      username: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
    });
  }


  loginUser() {
    if (this.myLoginForm.valid) {
      if (this.myLoginForm.value.username == 'admin') {
        if (this.myLoginForm.value.password == '123') {
          localStorage.useradmin_data = 'adminLogin'
          this.router.navigate(['/main']);
        } else {
          this.openSnackBar('password invalid');
        }
      } else {
        this.openSnackBar('username invalid');
      }
    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }

}
