import { Component, OnInit } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';
import { DialogDeleteDialog } from 'src/app/dialog/deletebox/delete.dialog';
import { DialogLogoutConfirmDialog } from 'src/app/dialog/logoutconfirm/logout.dialog';
@Component({
  selector: 'app-mainpage',
  templateUrl: './mainpage.component.html',
  styleUrls: ['./mainpage.component.css']
})
export class MainpageComponent implements OnInit {
  userData: any = {};
  allClassList: any = [];
  panelOpenState = false;
  constructor(private http: HttpClient, private router: Router, public dialog: MatDialog, private db: AngularFirestore) { }
  ngOnInit() {
    if (localStorage && localStorage.useradmin_data && localStorage.useradmin_data.length) {
      this.router.navigate(['/main/home']);
      // this.userData = JSON.parse(localStorage.useradmin_data);
    } else {
      this.router.navigate(['/login']);
    }
    // this.getAllClassList(this.userData);
  }
  getAllClassList(value) {
    let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes');
    ref.snapshotChanges().subscribe((res: any) => {
      if (res && res.length) {
        // res[0].payload.doc.data()
        this.allClassList = res;
        console.log(this.allClassList);
      }
    })
  }
  logoutUser() {
    const dialogRef = this.dialog.open(DialogLogoutConfirmDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: '', status: 'add' }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 'ok') {
        localStorage.removeItem("useradmin_data");
        this.router.navigate(['/login']);
      }
    });
  }
  navigate(ind) {
    this.router.navigate(['grade'], { queryParams: { jwt: JSON.stringify(ind) } });
  }
  deleteClass(evt, className) {
    evt.stopPropagation();
    evt.preventDefault();
    const dialogRef = this.dialog.open(DialogDeleteDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: '', status: 'add' }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === 'delete') {
        let value = JSON.parse(localStorage.useradmin_data);
        let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes').doc(className);
        ref.delete().then(() => {
          console.log('deleted');
        })
      }

    });
  }

}
