import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DialogDeleteDialog } from '../../dialog/deletebox/delete.dialog';
import { AngularFirestore } from '@angular/fire/firestore';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ThemePalette } from '@angular/material/core';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  allSchools: any = [];
  isDataLoaded: boolean = false;
  slideColor = 'primary';
  currentData: any;
  constructor(public dialog: MatDialog, private db: AngularFirestore) { }

  ngOnInit() {
    this.getAllSchool();
  }

  getAllSchool() {
    let ref = this.db.collection('allschool').doc('admin').collection('school');
    ref.snapshotChanges().subscribe((res: any) => {
      this.isDataLoaded = false;
      if (res && res.length) {
        this.allSchools = res;
        console.log(this.allSchools);
      }
    })
  }
  openConformDialog(value?, index?) {
    const dialogRef = this.dialog.open(DialogDeleteDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: value, ind: index }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result == 'ok') {
        let isApprovedVal = !value.isApproved;
        let ref1 = this.db.collection('allschool').doc('admin').collection('school').doc(value.schoolName);
        ref1.update({ isApproved: isApprovedVal }).then((res) => {
          console.log('added');
        });
        let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('info');
        ref.update({ isApproved: isApprovedVal }).then((res) => {
          console.log('added');
        });
        let ref3 = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection('school').doc(value.schoolName);
        ref3.update({ isApproved: isApprovedVal }).then((res) => {
          console.log('added');
        });
      }

    });
  }

}
