import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';


@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(public db: AngularFirestore) { }

  registerUser(value) {
    var timeStamp = new Date().getTime();
    value.id = timeStamp.toString();
    value.isApproved = false;
    return new Promise((resolve, reject) => {
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('info');
      ref.get().subscribe((res: any) => {
        if (res && res.data()) {
          resolve("registrated");
        } else {
          ref.set(value).then((res) => {
            resolve(res);
          })
          this.addSchoolToDistrict(value);
        }
      });
    })

  }
  addSchoolToDistrict(value) {
    let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection('school').doc(value.schoolName).set(value);

    let ref1 = this.db.collection('allschool').doc('admin').collection('school').doc(value.schoolName);
    ref1.set(value).then((res) => {
      console.log('added');
    })

  }

  getAllDistrictSchool(value) {
    return new Promise((resolve, reject) => {
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection('school');
      ref.get().subscribe((res: any) => {
        if (res && res.docs.length) {
          resolve(res.docs);
        } else {
          resolve(res.docs);
        }
      });
    })
  }

  getCurrentSchool(value) {
    return new Promise((resolve, reject) => {
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('info');
      ref.get().subscribe((res: any) => {
        if (res && res.data()) {
          resolve(res.data());
        } else {
          resolve('nodata');
        }
      });
    })
  }


}
